import React, { Component } from 'react';
import {connect} from 'react-redux';
import $ from 'jquery';
import axios from 'axios';
import Movies from './Movies';
import CircularProgress from 'material-ui/CircularProgress';
import Footer from '../components/Footer';
import { setResponseAsState , clearDataState , loadMoreOfGenre } from '../actions/moviesActions';
class TopMovies extends Component {
  componentDidMount() {
    $(document).ready(function(){
        $(this).scrollTop(0);
      	setTimeout(()=>{ $(".circle_progress_container").fadeOut(200);},500)
    });
    this.pageCounter = 2;
    this.props.displayTopMovies()
  }
  loadNextMovies = (page) => {
    this.props.loadNextMovies(page)
    this.pageCounter++;
  }
  componentWillUnmount() {
    this.props.clearDataStateFunction()
  }
  render() {
    return (
      <div>
        <div className="circle_progress_container ">
          <CircularProgress size={40} thickness={5} color="#bf2127"/>
        </div>
        <p className="jumbotron_title">TOP RATED</p>
        <Movies/>
        <div style={{display:"flex",justifyContent:"center"}}>
          {(this.props.data.length > 1)?<button className="genre_button" style={{marginTop:"45px"}} onClick={()=>this.loadNextMovies(this.pageCounter)}>load more</button>:null}
        </div>
        <Footer/>
      </div>
    );
  }

}
const mapStateToProps = (state) => {
  return {
    data: state.moviesReducer.data,
    genres:state.moviesReducer.genres,
    currentGenrePicked: state.moviesReducer.currentGenrePicked,
    activeTab: state.moviesReducer.activeTab
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    loadNextMovies: (page) => {
      axios.get("https://api.themoviedb.org/3/movie/top_rated?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&page="+page)
      .then(response => {
        console.log(response.data);
        console.log(response.data.results);
        dispatch(loadMoreOfGenre(response.data.results))
      })
      .catch(e => console.log(e))
    },
    displayTopMovies: () => {
      axios.get("https://api.themoviedb.org/3/movie/top_rated?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&page=1")
      .then(response => {
        console.log(response.data);
        console.log(response.data.results);
        dispatch(setResponseAsState(response.data.results))
      })
      .catch(e => console.log(e))
    },
    clearDataStateFunction: () => {
      dispatch(clearDataState())
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(TopMovies);



