import React, { Component } from 'react';
import './App.css';
import logo from './assets/logo2.png';
import $ from 'jquery';
import {connect} from 'react-redux';
import Discover from './containers/Discover';
import Home from './containers/Home';
import TopMovies from './containers/TopMovies';
import Upcoming from './containers/Upcoming';
import Movie from './components/Movie';


import { Route, BrowserRouter, Link, Redirect, Switch } from 'react-router-dom'

import {Tabs, Tab} from 'material-ui/Tabs';
import FloatingActionButton from 'material-ui/FloatingActionButton';
import AppBar from 'material-ui/AppBar';
import IconMenu from 'material-ui/IconMenu';
import IconButton from 'material-ui/IconButton';
import MenuItem from 'material-ui/MenuItem';
import FlatButton from 'material-ui/FlatButton';
import RaisedButton from 'material-ui/RaisedButton';
import MoreVertIcon from 'material-ui/svg-icons/navigation/more-vert';
import HabmurgerMenu from 'material-ui/svg-icons/navigation/menu';
import MoviesIcon from 'material-ui/svg-icons/action/theaters';
import TvIcon from 'material-ui/svg-icons/hardware/tv';
import GoUpArrow from 'material-ui/svg-icons/hardware/keyboard-arrow-up';


class App extends Component {
  componentDidMount() {
    $(document).ready(()=>{

      $(".go_to_top_button_holder").on("click",()=>{
        $("html, body").animate({ scrollTop: 0 }, 1000);
      })
    })
  }
  displayRandomLink = (array) => {
    if(array.length>=1){
      return (
        <Link to={"/movie/"+array[Math.floor(Math.random() * 19) + 1].id} style={{textDecoration:"none"}}>
          <MenuItem primaryText="Random" />
        </Link>
      )
    }else return null;
  }
  render() {
    return (
      <div>
        <BrowserRouter>
          <div>
          <div className="app_bar_wraper"><AppBar
            iconElementLeft={
              <Link to="/" style={{textDecoration:"none"}}>
                <img alt="ff" src={logo} className="logo"/>
              </Link>
            }
            iconElementRight={
              <IconMenu
                iconButtonElement={
                  <IconButton><HabmurgerMenu style={{transform:"scale(1.2)"}}/></IconButton>
                }
                targetOrigin={{horizontal: 'right', vertical: 'top'}}
                anchorOrigin={{horizontal: 'right', vertical: 'top'}}
              >
                <Link to="/" style={{textDecoration:"none"}}>
                  <MenuItem primaryText="Home" />
                </Link>
                <Link to="/toprated" style={{textDecoration:"none"}}>
                  <MenuItem primaryText="Top Rated" />
                </Link>
                <Link to="/discover" style={{textDecoration:"none"}}>
                  <MenuItem primaryText="Discover" />
                </Link>
                <Link to="/upcoming" style={{textDecoration:"none"}}>
                  <MenuItem primaryText="Upcoming" />
                </Link>
                {
                  this.displayRandomLink(this.props.moviesForSliders)
                }
              </IconMenu>
            }
          /></div>

          <FloatingActionButton mini={true} className="go_to_top_button_holder" secondary>
            <GoUpArrow />
          </FloatingActionButton>


          <Switch>
            <Route  path='/' exact component={Home} />
            <Route  path='/discover' exact component={Discover} />
            <Route  path='/toprated' exact component={TopMovies} />
            <Route  path='/upcoming' exact component={Upcoming} />
            <Route  path='/movie/:movieId'  component={Movie} />
          </Switch>
        </div>
      </BrowserRouter>
    </div>
    );
  }
}


const mapStateToProps = (state) => {
  return {
    moviesForSliders : state.moviesSlidersReducer.moviesForSliders,
  }
}





export default connect(mapStateToProps)(App);



















