const moviesReducer = (
  state={
    data:[],
    genres:[],
    currentGenrePicked:"10759",
    thisMovieIdData:{
      genres:[{name:null},{name:null},{name:null}],
      release_date:''
    },
    thisMovieIdDataYOUTUBE:{
      results:[{key:"1"}],
    },
    thisMovieIdDataSIMILAR:[{title:"blank"}],

  },
  action ) => {

  switch (action.type) {
case "SET_RESPONSE_AS_STATE":
      state={
        ...state,
        data: action.payload,
      };
      break;
case "SET_GENRES":
      state={
        ...state,
        genres: action.payload,
      };
      break;
case "PICK_GENRE":
      state={
        ...state,
        data: action.payload,
        currentGenrePicked: action.currentGenrePicked
      };
      break;
case "LOAD_MORE_OF_GENRE":
      state={
        ...state,
        data: state.data.concat(action.payload),
      };
      break;
case "CLEAR_DATA":
      state={
        ...state,
        data: action.payload,
      };
      break;
case "CLEAR_MOVIE_DATA_ON_UNMOUNT":
      state={
        ...state,
        thisMovieIdData: {
          genres:[{name:null},{name:null},{name:null}],
          release_date:'hgh'
        },
        thisMovieIdDataYOUTUBE: {
          results:[{key:"1"},{key:"2"}],
        }
      };
      break;
case "GET_MOVIE_DATA_BASED_ON_ID":
      state={
        ...state,
        thisMovieIdData: action.payload,
      };
      break;
case "GET_MOVIE_YOUTUBE_DATA_BASED_ON_ID":
      state={
        ...state,
        thisMovieIdDataYOUTUBE: action.payload
      };
      break;
case "GET_MOVIE_SIMILAR_DATA_BASED_ON_ID":
      state={
        ...state,
        thisMovieIdDataSIMILAR: action.payload
      };
      break;
    }
    return state;
}
export default moviesReducer;



